<template>
  <v-app>
    <v-navigation-drawer
      app
      fixed
      v-model="nav.drawer"
    >
      <v-list>
        <v-list-tile
          v-for="(item, i) in nav.items"
          :key="i"
          exact
          @click="scrollTo(item.route)">
          <v-list-tile-action>
            <v-icon v-html="item.icon"></v-icon>
          </v-list-tile-action>
          <v-list-tile-content>
            <v-list-tile-title v-text="item.title"></v-list-tile-title>
          </v-list-tile-content>
        </v-list-tile>
      </v-list>
    </v-navigation-drawer>

    <v-toolbar app fixed>
      <v-toolbar-title v-text="title"></v-toolbar-title>
      <v-spacer></v-spacer>
      <v-toolbar-side-icon class="hidden-md-and-up" @click.native.stop="nav.drawer = !nav.drawer"></v-toolbar-side-icon>
      <v-toolbar-items class="hidden-sm-and-down">
        <v-btn flat @click="$vuetify.goTo('#home')">Home</v-btn>
        <v-btn flat @click="$vuetify.goTo('#about')">About</v-btn>
        <v-btn flat @click="$vuetify.goTo('#portfolio')">Portfolio</v-btn>
        <v-btn flat @click="$vuetify.goTo('#contact')">Contact</v-btn>
      </v-toolbar-items>
    </v-toolbar>

    <v-content>
      <poly-background/>
      <section>
        <v-jumbotron height="450px">
          <v-container fill-height>
            <v-layout align-center text-xs-center white--text>
              <v-flex>
                <h3 class="display-3">itho</h3>
                <span class="subheading">Web &amp; Mobile Development.</span>
              </v-flex>
            </v-layout>
          </v-container>
        </v-jumbotron>
      </section>
      
      <!-- <v-slide-x-transition mode="out-in">
        <router-view/>
      </v-slide-x-transition> -->

      <home-view id="home"/>
      <about-view id="about"/>
      <portfolio-view id="portfolio"/>
      <contact-view id="contact"/>
    </v-content>
    
    <v-footer app absolute>
      <span class="pl-2">&copy; {{ new Date().getFullYear() }}</span>
    </v-footer>
  </v-app>
</template>

<script>
  import HomeView from '@/views/Home'
  import AboutView from '@/views/About'
  import PortfolioView from '@/views/Portfolio'
  import ContactView from '@/views/Contact'
  import PolyBackground from '@/components/Poly.vue'

  export default {
    components: {
      HomeView,
      AboutView,
      PortfolioView,
      ContactView,
      PolyBackground
    },
    data () {
      return {
        title: 'itho',
        nav: {
          drawer: false,
          items: [
            { title: 'Home', icon: 'home', route: 'home' },
            { title: 'About', icon: 'help', route: 'about' },
            { title: 'Portfolio', icon: 'list', route: 'portfolio' },
            { title: 'Contact', icon: 'feedback', route: 'contact' }
          ],
          mini: false,
          right: null
        }
      }
    },
    methods: {
      scrollTo (id) {
        this.$vuetify.goTo(id)
      }
    }
  }
</script>



